import React, { useState } from 'react'
import { addCard, editCardFirebase, storeCardImg } from '../../../../Store/ActionCreators';
import { Spinner } from 'reactstrap';

import './EditCard.css'
import { connect } from 'react-redux';
import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
const editCard = (props) => {

    let isAdded = props.isAdded;


    const [templateName, settemplateName] = useState('')
    const [category, setcategory] = useState('')
    const [price, setprice] = useState(0)
    const [des, setdes] = useState('')
    const [errorColor, seterrorColor] = useState('black')
    const [addClicked, setaddClicked] = useState(false)
    const [isEditDisabled, setisEditDisabled] = useState(false)
    const [editClicked, seteditClicked] = useState(false)



    // UPLOAD IMAGE AREA------------------------------------------------------


    const [imageAsFile, setImageAsFile] = useState('')

    const checkImageFormat = (imageFile) => {
        if (typeof imageFile === 'object') {
            return imageFile.type.includes("image")
        } else if (typeof imageFile === 'undefined') {
            return false
        } else {
            return false
        }
    }



    const handleImageAsFile = (e) => {
        setaddClicked(false)
        seteditClicked(false)
        props.isAddedFalse()
        props.isEditedFalse()
        const image = e.target.files[0]
        setImageAsFile(image)
        console.log(image)
        if (!checkImageFormat(image)) {
            alert('Please Upload file in image format only!')
            seterrorColor('red')
        } else {
            seterrorColor('green')
        }
    }


    //------------------------------------------------------------------------

    // This is to Toggle add modal
    // const toggleAdd = () => {
    //     if (props.isAdded) {
    //         props.toggleSubmit()
    //     }
    // }

    const storeIfNotEmpty = (e, func) => {
        setaddClicked(false)
        seteditClicked(false)
        setisEditDisabled(false)
        props.isAddedFalse()
        props.isEditedFalse()
        let value = e.target.value;
        if (value !== '') {
            func(value);
        }
    }

    const isUpdate = (prev, curr) => {
        if (curr === '' || curr === 0) {
            return prev;
        } else {
            return curr
        }
    }
    const editCardHandler = () => {

        seteditClicked(true)

        let editedCard = {
            id: props.id,
            templateName: isUpdate(props.name, templateName),
            category: isUpdate(props.category, category),
            price: isUpdate(props.price, price),
            des: isUpdate(props.des, des)
        }


        if (imageAsFile === '' || imageAsFile === undefined) {
            console.log('Empty IMG Field')
            props.editcard(editedCard, props.imgURL, props.imgName)

        } else if (imageAsFile.type.includes("image")) {
            console.log('Image Type')
            props.editcard(editedCard, imageAsFile, props.imgName)

        } else {
            console.log('Unknown Type')
            props.editcard(editedCard, props.imgURL, props.imgName)
        }

    }

    const addNewCardHandler = () => {
        setaddClicked(true)
        props.addNewCard({
            templateName: templateName,
            category: category,
            price: price,
            des: des
        }, imageAsFile)

    }

    const isInputFieldValidForAdd = () => {
        return templateName !== '' && price !== 0 && des !== '' && category !== '' && imageAsFile !== '' && checkImageFormat(imageAsFile)
    }

    console.log(props.type)

    let savebtn = null;
    if (props.type === 'add') {
        savebtn = (
            <div>
                <Button style={{ width: '58px' }} disabled={!isInputFieldValidForAdd()} onClick={() => {
                    addNewCardHandler()

                }} color="primary" >{isInputFieldValidForAdd() && addClicked && !props.isAdded ? <Spinner size="sm" color="light" /> : <p style={{ padding: '0', margin: '0' }}>Add</p>}</Button>
                <Button onClick={props.modalOff} color="secondary">Cancel</Button>
            </div>
        )
    } else if (props.type === 'edit') {
        savebtn = (
            <div>
                <Button onClick={() => {
                    editCardHandler()
                }} color="primary" >{editClicked && !props.isEdited ? <Spinner size="sm" color="light" /> : <p style={{ padding: '0', margin: '0' }}>Save</p>}</Button>
                <Button onClick={props.toggleSubmit2} color="secondary">Cancel</Button>
            </div>

        )
    }
    return (
        <div className="editCard-main-div">
            <Form>
                <FormGroup>
                    <Label>Template Name</Label>
                    <Input onChange={(e) => storeIfNotEmpty(e, settemplateName)} type="text" name="templatename" placeholder={props.name} />
                </FormGroup>

                <FormGroup>
                    <Label>Category</Label>
                    <Input onChange={(e) => storeIfNotEmpty(e, setcategory)} type="text" name="category" placeholder={props.category} />
                </FormGroup>

                <FormGroup>
                    <Label>Price</Label>
                    <Input onChange={(e) => storeIfNotEmpty(e, setprice)} type="number" name="price" placeholder={props.price} />
                </FormGroup>

                <FormGroup>
                    <Label >Description</Label>
                    <Input onChange={(e) => storeIfNotEmpty(e, setdes)} type="textarea" name="text" placeholder={props.des} />
                </FormGroup>

                <FormGroup>
                    <Label>Upload Card</Label>
                    <Input style={{ color: errorColor }} onChange={handleImageAsFile} type="file" name="file" />
                </FormGroup>
            </Form>

            {savebtn}

        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        isAdded: state.ui.isCardAddedSuccess,
        isEdited: state.ui.isEdited
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        isAddedFalse: () => dispatch({ type: 'ADDED_FALSE' }),
        isEditedFalse: () => dispatch({ type: 'EDITED_FALSE' }),
        addNewCard: (card, img) => dispatch(addCard(card, img)),
        editcard: (card, img, prevImg) => dispatch(editCardFirebase(card, img, prevImg))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(editCard);